* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}


.country-list {
padding-left: 2px;
}

.list__item {
  margin-top: 10px;
  display: flex;
  align-items: center;
  padding-left: 0px;
}

.list__name {
  margin-left: 10px;
  font-weight: 500;
}

.info__names {
  font-weight: 700;
}

.info__values {
  font-weight: 500;
}

.info__card {
  display: flex;
  align-items: center;
  font-weight: 700;
  font-size: 30px;
}

.info__img {
  margin-right: 10px;
}